html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: "Inter Tight", sans-serif;
  background-color: #eff3f5;
  color: #0a0f13;
}

.container {
  width: 1800px;
  margin: 0 auto;
}

.header-content {
  width: 100%;
  height: 468px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}
.header-content .hero__logo {
  position: absolute;
  top: 24px;
  z-index: 2;
}
.header-content .hero__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.main-content__nav-filter {
  display: flex;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.footer-content .contacts__name {
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 28.8px */
  letter-spacing: -0.48px;
  margin-bottom: 23px;
}
.footer-content .contacts__text {
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%; /* 24px */
  letter-spacing: -0.4px;
}

.footer-contacts {
  width: 50%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 4fr;
  gap: 20px;
  justify-items: start;
}
.footer-contacts .contacts__title {
  grid-column: 1/-1;
  grid-row: 1/2;
  width: 100%;
}
.footer-contacts .contacts__title .contacts__title-decoration {
  display: flex;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
}
.footer-contacts .contacts__title .contacts__title-decoration div {
  height: 1px;
  flex: 1 0 0;
  background-color: #0a0f13;
}
.footer-contacts .contacts__title .contacts__title-decoration p {
  text-transform: uppercase;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%; /* 19.6px */
  letter-spacing: -0.28px;
}
.footer-contacts .contacts__title .contacts__title-name {
  font-size: 96px;
  font-style: normal;
  font-weight: 400;
  line-height: 90%; /* 86.4px */
  letter-spacing: -3.84px;
  margin-top: 25px;
  margin-bottom: 83px;
}
.footer-contacts .contacts__content-office {
  grid-column: 1/2;
  grid-row: 2/3;
}
.footer-contacts .contacts__content-btn-map {
  grid-column: 1/-1;
  grid-row: 3/4;
  margin-bottom: 84px;
}
.footer-contacts .contacts__content-tels {
  grid-column: 1/2;
  grid-row: 4/5;
}
.footer-contacts .contacts__content-tels .tel-number {
  margin-left: 36px;
  margin-bottom: 15px;
}
.footer-contacts .contacts__content-tels .tel-number span {
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  color: #737373;
  line-height: 140%; /* 19.6px */
  letter-spacing: -0.28px;
  text-transform: uppercase;
  margin-left: 12px;
}
.footer-contacts .contacts__content-medias {
  grid-column: 2/3;
  grid-row: 2/3;
}
.footer-contacts .contacts__content-medias .contacts__content-medias-icons {
  display: flex;
  justify-content: start;
  gap: 4px;
}
.footer-contacts .contacts__content-email {
  grid-column: 2/2;
  grid-row: 4/5;
}

.btn--black {
  display: inline-block;
  padding: 13px 20px;
  background: #0a0f13;
  color: #eff3f5;
  border-radius: 100px;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.2s;
}
.btn--black:hover, .btn--black:focus {
  background: #222;
  color: #fff;
}

.footer-img {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-img .footer-img__picture {
  width: 830px;
  height: 680px;
}